const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
const { 
  AlignmentToolbar,
  BlockControls,
  InspectorControls,  
  RichText, 
  URLInputButton } = wp.editor;
const { 
  IconButton,
  PanelBody } = wp.components;

import { ReactComponent as Logo } from "../vimeo_icon.svg";

const vimeoURL = "https://player.vimeo.com/video/393485254";


registerBlockType("wpme/vimeo", {
  title: __("GDPR Compliant Vimeo Player", "wpme"),
  icon: { src: Logo },
  category: "wpme",
  attributes: {
    episodeURL: {
      type: "string",
      source: "attribute",
      selector: ".wpme-cta a",
      attribute: "href"
    },
    vimeoURL: {
      type: "string",
      source: "attribute",
      selector: ".wp-block-embed iframe",
      attribute: "src",
      default: vimeoURL
    }    
  },
  supports: {
    align: ["left","right","center"]
  },

  edit: props => {

    // Lift info from props and populate various constants.
    const {
      attributes: { 
        episodeImage, 
        episodeURL,
        vimeoURL
       },
      className,
      setAttributes
    } = props;


    // Grab imageObject, set the value of episodeImage to imageObject.sizes.wpmeFeatImg.url.
    const onImageSelect = imageObject => {
      setAttributes({ episodeImage: imageObject.sizes.wpmeFeatImg.url });
    };


    // Grab newEpisodeURL, set the value of episodeURL to newEpisodeURL.
    const onChangeEpisodeURL = newEpisodeURL => {
      setAttributes({ episodeURL: newEpisodeURL });

      var youVimeoVideoID = newEpisodeURL.replace("https://vimeo.com/",'');
      var youVimeoVideoID = youVimeoVideoID.replace("amp;",'');
      var youVimeoVideoID = youVimeoVideoID.replace("&#038;",'');
      var youVimeoVideoID = youVimeoVideoID.replace("&",'');


      var newVimeoURL     = "https://player.vimeo.com/video/" + youVimeoVideoID + "?dnt=true";
      setAttributes({ vimeoURL: newVimeoURL });

    };


    return [
      <div 
        className={`${className} wpme-block wpme-expanded`}
        >
        <figure class="wp-block-embed-youtube wp-block-embed is-type-video is-provider-youtube wp-embed-aspect-16-9 wp-has-aspect-ratio">
          <div class="wp-block-embed__wrapper">
            <iframe src={vimeoURL} title="vimeo-player" class="resp-iframe" allowfullscreen="" width="500" height="281" frameborder="0"></iframe>
          </div>
        </figure>
        <div className="wpme-cta">
          {/* Note: href is NOT populated with attribute to avoid
              accidental navigation from within the editor. */}
          <a href="#"></a>
          <URLInputButton
            className="wpme-dropdown__input"
            label={__("Episode URL", "wpme")}
            onChange={onChangeEpisodeURL}
            url={episodeURL}
          />
        </div>
      </div>
    ];
  },

  save: props => {
    const {
      attributes: {  
        episodeImage, 
        episodeURL,
        vimeoURL
      }
    } = props;

    return (
      <div className="wpme-block wpme-expanded">
        <figure class="wp-block-embed-youtube wp-block-embed is-type-video is-provider-youtube wp-embed-aspect-16-9 wp-has-aspect-ratio">  
          <div class="wp-block-embed__wrapper">
            <iframe src={vimeoURL} title="vimeo-player" class="resp-iframe" allowfullscreen="" width="500" height="281" frameborder="0"></iframe>
          </div>
        </figure>
        <div className="wpme-cta">
          {/* Note: href IS populated with attribute here
              because this is where the link is saved. */}
          <a href={episodeURL}></a>
        </div>
      </div>
    );
  }
});
