const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
const { 
  AlignmentToolbar,
  BlockControls,
  InspectorControls,  
  RichText, 
  URLInputButton } = wp.editor;
const { 
  IconButton,
  PanelBody } = wp.components;

import { ReactComponent as Logo } from "../yt_logo_rgb_light.svg";

const youTubeURL = "https://www.youtube-nocookie.com/embed/jyImWMzQw88?modestbranding=1&#038;rel=0&#038;showinfo=0";


registerBlockType("wpme/youtube", {
  title: __("GDPR Compliant YouTube Player", "wpme"),
  icon: { src: Logo },
  category: "wpme",
  attributes: {
    episodeURL: {
      type: "string",
      source: "attribute",
      selector: ".wpme-cta a",
      attribute: "href"
    },
    youTubeURL: {
      type: "string",
      source: "attribute",
      selector: ".wp-block-embed iframe",
      attribute: "src",
      default: youTubeURL
    }    
  },
  supports: {
    align: ["left","right","center"]
  },

  edit: props => {

    // Lift info from props and populate various constants.
    const {
      attributes: { 
        episodeImage, 
        episodeURL,
        youTubeURL
       },
      className,
      setAttributes
    } = props;


    // Grab imageObject, set the value of episodeImage to imageObject.sizes.wpmeFeatImg.url.
    const onImageSelect = imageObject => {
      setAttributes({ episodeImage: imageObject.sizes.wpmeFeatImg.url });
    };


    // Grab newEpisodeURL, set the value of episodeURL to newEpisodeURL.
    const onChangeEpisodeURL = newEpisodeURL => {
      setAttributes({ episodeURL: newEpisodeURL });

      var youTubeVideoID = newEpisodeURL.replace("https://youtu.be/",'');
      var youTubeVideoID = youTubeVideoID.replace("watch?v=",'');
      var youTubeVideoID = youTubeVideoID.replace("https://www.youtube.com/",'');
      var youTubeVideoID = youTubeVideoID.replace("feature=youtu.be",'');
      var youTubeVideoID = youTubeVideoID.replace("amp;",'');
      var youTubeVideoID = youTubeVideoID.replace("&#038;",'');
      var youTubeVideoID = youTubeVideoID.replace("&",'');


      var newYouTubeURL     = "https://www.youtube-nocookie.com/embed/" + youTubeVideoID + "?modestbranding=1&#038;rel=0&#038;showinfo=0";
      setAttributes({ youTubeURL: newYouTubeURL });

    };


    return [
      <div 
        className={`${className} wpme-block wpme-expanded`}
        >
        <figure class="wp-block-embed-youtube wp-block-embed is-type-video is-provider-youtube wp-embed-aspect-16-9 wp-has-aspect-ratio">
          <div class="wp-block-embed__wrapper">
            <iframe src={youTubeURL} class="resp-iframe" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen="" width="500" height="281" frameborder="0"></iframe>
          </div>
        </figure>
        <div className="wpme-cta">
          {/* Note: href is NOT populated with attribute to avoid
              accidental navigation from within the editor. */}
          <a href="#"></a>
          <URLInputButton
            className="wpme-dropdown__input"
            label={__("Episode URL", "wpme")}
            onChange={onChangeEpisodeURL}
            url={episodeURL}
          />
        </div>
      </div>
    ];
  },

  save: props => {
    const {
      attributes: {  
        episodeImage, 
        episodeURL,
        youTubeURL
      }
    } = props;

    return (
      <div className="wpme-block wpme-expanded">
        <figure class="wp-block-embed-youtube wp-block-embed is-type-video is-provider-youtube wp-embed-aspect-16-9 wp-has-aspect-ratio">  
          <div class="wp-block-embed__wrapper">
            <iframe src={youTubeURL} class="resp-iframe" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen="" width="500" height="281" align="middle" frameborder="0"></iframe>
          </div>
        </figure>
        <div className="wpme-cta">
          {/* Note: href IS populated with attribute here
              because this is where the link is saved. */}
          <a href={episodeURL}></a>
        </div>
      </div>
    );
  }
});
